// AIMBOT

const [counter, setCounter] = useState(100); 

var changeKills = (amount) => {

  setCounter(prevCounter => prevCounter + 25);
  kentoData[4].kills = +kentoData[4].kills + amount; 
  kentoData[4].headshots = +kentoData[4].headshots + (amount / 4); 
//  forceUpdate()    
}


var aimbotAdd = () => { 
var i = 0
const interval = setInterval(() => {

  if (i > 4) {  
    clearInterval(interval);
  }
  
   changeKills(25); 
   i++ 
}, 100);

}

var aimbotSubtract = () => { 
var i = 0
  const interval = setInterval(() => {

    if (i > 4) {  
      clearInterval(interval);
    }
    
     changeKills(-25); 
     i++ 
  }, 100);

}


function checkBox()
{
  if (checked) 
  {
      setChecked(!checked)
      aimbotSubtract()
  } else {
     setChecked(true)
      aimbotAdd()
  }
}
